import React, { Component } from 'react';
import './App.css';
import Header from './components/Header';
import Hero from './components/Hero';
import About from './containers/About';
import Post from './containers/Post';
import project from './components/Projects/index';
import Navbar from './components/Navbar';
import AllPosts from './components/AllPosts';
import { BrowserRouter as Router, Switch, Route, Link, Redirect } from 'react-router-dom';

import MainPage from './pages';
import PageNotFound from './pages/404';

class App extends Component {
  componentDidUpdate() {
    let header = document.querySelector("h1");
    let text = header.textContent.toLowerCase();
    let color;
    switch (text) {
      case 'about':
        color = "#16A085";
        break;
      case 'projects':
        color = "#E74C3C";
        break;
      case 'contacts':
        color = "#2980B9";
        break;
      case 'blog':
        color = "#2980B9";
        break;
      default:
        color = "#F1C40F";
        break;
    }
    let root = document.querySelector(":root");
    root.style.setProperty("--color-home", color);
  };
  render() {
    return (

      <Router>
        <div className="App">
          <Header />
          <nav className="Navbar">
            <h2>UJ</h2>
            <ul>
              <li>
                <Link to="/about">about</Link>
              </li>
              <li>
                <Link to="/projects">projects</Link>
              </li>
              <li>
                <Link to="/contacts">contacts</Link>
              </li>
              <li>
                <Link to="/">Blog</Link>
              </li>
            </ul>
          </nav>
          <Hero />

          <div className="container">

            <Switch>


              <Route exact path="/" component={MainPage} />
              <Route path="/about" component={About} />
              <Route exact path="/post/:postId" component={Post} />
              <Route component={PageNotFound} />
              <Route path="/about"><h1>About</h1></Route>
              <Route path="/projects" component={project}><h1>Projects</h1></Route>
              <Route path="/contacts"><h1>Contacts</h1></Route>
              <Route path="/" ><h1>Blog</h1></Route>
            </Switch>
          </div>

        </div>
      </Router>
    );
  }
}

export default App;
