import React, { useState, useEffect } from 'react';
import blogPost from '../../data/blog.json';

/**
* @author
* @function BlogPost
**/

const Projects = (props) => {

  const [Project, setPost] = useState({});

  //reload the content
  const [postId, setPostId] = useState("");

  useEffect(() => {
    const postId = props.match.params.postId;
    const Project = blogPost.data.find(post => post.id == postId);
    setPost(Project);
    setPostId(postId);
  }, [Project, props.match.params.postId]);

  if (Project.blogImage === "")
    return null;

  return (
    <div>
      <div className="blog-post-container">
        <h3 class="my-5">{Project.blogTitle}</h3>
        <div className="post-thumb">
          <span class="blog-post-tag">{Project.blogCategory}</span>
          <img src={Project.blogImage} style={{ width: "100%" }} />

          {/* <img src={require('../../assets/images/mustang.jpg')} /> */}
        </div>
        <div className="blog-post-text">

          <div>{Project.blogText}</div>
          <p className="mt-5 text-muted">Posted on: {Project.postedOn} by {Project.author}</p>
        </div>

      </div></div>

  )

}

export default Projects;
